:root {
  font-family: system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
  width: 100vw;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* index.css */

/* Applies only when inside a .custom-popup-single parent */
.custom-popup-single .mapboxgl-popup-content {
  background-color: #c12525 !important;
  /* dark red */
  color: white !important;
}

.custom-popup-single .mapboxgl-popup-tip {
  border-top-color: #b71c1c !important;
}

.custom-popup-multi .mapboxgl-popup-content {
  background-color: #1e3a8a !important;
  /* dark blue */
  color: white !important;
}

.custom-popup-multi .mapboxgl-popup-tip {
  border-top-color: #1e3a8a !important;
}

/* Optional: shared styles */
.mapboxgl-popup-content {
  border-radius: 8px !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 12px !important;
  max-width: 240px;
}



a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}

a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  display: flex;
  place-items: center;
  width: 100vw;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  cursor: pointer;
  transition: border-color 0.25s;
}

button:hover {
  border-color: #646cff;
}

button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }

  a:hover {
    color: #747bff;
  }

  button {
    background-color: #f9f9f9;
  }
}